import React from "react";
import { Stack, Box, Typography } from "@mui/material";

import { ChannelCard, Loader, VideoCard } from "./";

const Videos = ({ videos, direction }) => {
  if (!videos) {
    return <Typography>Loading videos...</Typography>;
  }
  return (
    <Stack direction={direction || "row"} flexWrap="wrap" justifyContent="start" gap={4}>
      {videos.map((item, idx) => {
        const isVideo = item.id && item.id.videoId;
        const isChannel = item.id && item.id.channelId;
        if (isVideo || isChannel) {
          return (
            <Box key={idx} sx={{ width: { xs: '100%', sm: '358px', md: "320px" }, m: 0, p: 0 }}>
              {isVideo && <VideoCard video={item} />}
              {isChannel && <ChannelCard channelDetail={item} marginTop="0" />}
            </Box>
          );
        }
        return null;
      })}
      {videos.length === 0 && (
        <Typography sx={{ m: 2 }}>No videos or channels available.</Typography>
      )}
    </Stack>
  );
};
export default Videos;